html, body {
  margin: 0;
  height: 100%;
}
body {
  font-size:62.5%;  /* 1 rem = 10px */
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  font-size: 1rem;
}
@font-face{
    font-family: 'Fira Code';
    src: url(/micro-gg/static/media/FiraCode-Light.259c44f1.eot);
    src: url(/micro-gg/static/media/FiraCode-Light.259c44f1.eot) format('embedded-opentype'),
         url(/micro-gg/static/media/FiraCode-Light.3a7fa128.woff2) format('woff2'),
