.debug-colliding {
  width: 32px;
  height: 32px;
  position: relative;
  top: -32px; }

.number {
  text-align: center;
  vertical-align: middle;
  position: absolute;
  font-size: .9rem;
  line-height: .9rem;
  padding: 0 2px;
  opacity: 0; }

.number.top {
  top: -17px;
  left: 9.5px; }

.number.bottom {
  top: 2px;
  left: 9.5px; }

.number.left {
  top: -7.5px;
  left: 0px; }

.number.right {
  top: -7.5px;
  left: 19px; }

.active {
  background-color: deeppink;
  opacity: 1; }


.sprite {
  width: 0;
  height: 0;
}
h1 {
  font-size: 1.6rem; }

/* React app is injected into this container (I think) */
#root {
  height: 100%; }

/* container added for the css variable injection */
#root > div {
  height: 100%; }

/* react-hotkeys adds this container, doesn't have any className to grab onto */
div[tabindex="-1"] {
  height: 100%;
  display: flex; }

div[tabindex="-1"]:focus {
  outline: 0; }

.main {
  display: flex;
  min-height: 100%;
  background-color: var(--primary-color); }

.left, .right {
  display: flex;
  flex-direction: column; }

.left {
  flex-grow: 1;
  background-color: var(--dark-color); }

@media only screen and (max-width: 900px) {
  .main {
    flex-direction: column-reverse; }
  /* react-hotkeys container */
  div[tabindex="-1"] {
    /* The default 100% height was killing the layout */
    height: auto;
    background-color: var(--primary-color); } }

header {
  padding: 1rem 1rem 1rem 1rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  color: var(--primary-color); }

.left header {
  justify-content: space-between; }
  .left header h1 {
    margin: 0;
    margin-top: -5px;
    line-height: 0; }

button {
  border: none;
  border-radius: 0.4rem;
  height: 2rem;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 0.8rem;
  width: 6rem;
  margin-right: 1rem; }
  button:focus {
    outline: 0; }

button.primary {
  background-color: var(--dark-color);
  color: var(--primary-color); }

button.secondary {
  background-color: transparent;
  border: solid 2px var(--dark-color);
  color: var(--dark-color); }

.code, .game-container {
  display: flex;
  flex-direction: column; }

.code {
  flex-grow: 1; }
  .code .rule:hover {
    background-color: var(--hover-color); }

.stage {
  background-color: var(--dark-color);
  overflow: hidden; }

.error {
  color: red;
  padding: 2rem; }

.code {
  background-color: var(--dark-color);
  font-size: 1.0rem;
  font-family: 'Fira Code';
  padding: 1rem;
  color: var(--primary-color); }

.code-sprite {
  width: 16px;
  height: 16px;
  margin-left: .4rem;
  margin-right: .2rem; }

.tooltip-content .stage {
  display: flex;
  border: 1px solid #000000; }

.game-container {
  padding: 1rem;
  padding-top: 0;
  flex-direction: column; }

@media only screen and (max-width: 900px) {
  .game-container {
    border-left: none; } }

